<template>
    <div class="page">
        <div class="app-container">
            <el-row :gutter="20">
                <el-col :span="8">

                    <div class="user_img">
                        <img :src="loginMsg.avatar" alt="用户头像">
                        <p>
                            <button type="button" class="el-button filter-item el-button--primary"
                                    @click="uploadAvatar()">
                                <i class="fa fa-cloud-upload" aria-hidden="true"></i><span>上传头像</span>
                            </button>
                        </p>
                    </div>

                </el-col>

                <el-col :span="16">
                    <div class="user_info">
                        <h2>个人信息
                            <el-button size="small" v-if="!editable" @click="editable = true" style="float:right;"
                                       icon="edit">编辑
                            </el-button>
                        </h2>
                        <div class="user_info_form" v-if="editable">

                            <el-form label-position="right" label-width="100px" :model="loginMsg">

                                <el-form-item label="昵称">
                                    <el-input v-model="loginMsg.name"></el-input>
                                </el-form-item>
                                <el-form-item label="性别">
                                    <el-radio-group v-model="loginMsg.sex">
                                        <el-radio label="man">男</el-radio>
                                        <el-radio label="woman">女</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="token码">
                                    <el-input v-model="loginMsg.token" disabled=""></el-input>
                                </el-form-item>
                                <el-form-item label="个人简介">
                                    <el-input type="textarea" v-model="loginMsg.introduction"></el-input>
                                </el-form-item>

                                <el-form-item>
                                    <el-button type="primary" @click="submitForm()">提交</el-button>
                                    <el-button @click="resetForm()">取消</el-button>
                                </el-form-item>

                            </el-form>

                        </div>

                        <div class="user_info_text" v-else>
                            <el-form label-position="right" label-width="100px" :model="loginMsg">

                                <el-form-item label="昵称：">
                                    {{ loginMsg.name }}
                                </el-form-item>
                                <el-form-item label="性别：">
                                    <span v-if="loginMsg.sex == '男'">男</span>
                                    <span v-else>女</span>

                                </el-form-item>
                                <el-form-item label="token码">
                                    {{ loginMsg.token }}

                                </el-form-item>
                                <el-form-item label="个人简介：">
                                    {{ loginMsg.introduction }}

                                </el-form-item>


                            </el-form>
                        </div>
                    </div>

                </el-col>
            </el-row>

        </div>

    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    export default {
        data() {
            return {
                editable: false,

            }
        },
        computed: mapGetters(['loginMsg']),
        mounted() {

        },
        methods: {
            //提交表单
            submitForm() {
                var vm = this;
                vm.editable = false;
                var par = {
                    "status": vm.loginMsg.status,
                    "name": vm.loginMsg.name,
                    "token": vm.loginMsg.token,
                    "avatar":vm.loginMsg.avatar,
                    "sex": vm.loginMsg.sex,
                    "introduction": vm.loginMsg.introduction,
                };
                this.$store.commit("editUserINfo",par);
                
            },
            //取消
            resetForm(){
               this.editable=false;
            },
            //上传头像
            uploadAvatar(){
                alert('稍后补上')
            },
        }
    };
</script>

<style lang="scss">
    .app-container{
        margin:10px 1% 0;
        width:97%;
        overflow-x: hidden;
    }
    .user_img {
        /*width: 80%;*/
        padding: 20px;
        text-align: center;
        border: 1px solid #dee1e2;
        img {
            max-width: 200px;
            max-height: 200px;
            border-radius: 50%;
        }
    }
    .user_info_form label,.user_info_text label{
        font-weight: bold;
    }
    .user_info {
        padding-bottom: 20px;
        border: 1px solid #dee1e2;
        font-size:14px;
        h2 {
            margin: 0;
            font-weight: bold;
            padding: 10px 20px;
            border-bottom: 1px solid #dee1e2;
            .i_edit {
                float: right;
                font-size: 14px;
                color: #7ab8ed;
            }
        }
    }

    .user_info_form, .user_info_text {
        padding: 20px;
    }
</style>



